import React from "react";
import { RenderAfterNavermapsLoaded, NaverMap, Marker } from "react-naver-maps"; // Ìå®ÌÇ§ÏßÄ Î∂àÎü¨Ïò§Í∏∞

const NaverMapAPI = ({ x, y, positionsd }) => {
  const navermaps = window.naver.maps;

  return (
    <NaverMap
      mapDivId={"maps-getting-started-uncontrolled"} // default: react-naver-map
      style={{
        width: "70rem", // ÎÑ§Ïù¥Î≤ÑÏßÄÎèÑ Í∞ÄÎ°ú Í∏∏Ïù¥
        height: "85vh", // ÎÑ§Ïù¥Î≤ÑÏßÄÎèÑ ÏÑ∏Î°ú Í∏∏Ïù¥
      }}
      defaultCenter={new navermaps.LatLng(Number(y), Number(x))}
      defaultZoom={16} // ÏßÄÎèÑ Ï¥àÍ∏∞ ÌôïÎåÄ Î∞∞Ïú®
    >
      <Marker
        key={1}
        position={new navermaps.LatLng(Number(y), Number(x))}
        animation={2}
        onClick={() => {
          alert(positionsd);
        }}
      />
    </NaverMap>
  );
};

const NaverMapDis = ({ x, y }) => {
  return (
    <>
      <RenderAfterNavermapsLoaded
        ncpClientId={process.env.REACT_APP_NAVERPASSWORD} // ÏûêÏã†Ïùò ÎÑ§Ïù¥Î≤Ñ Í≥ÑÏ†ïÏóêÏÑú Î∞úÍ∏âÎ∞õÏùÄ Client ID
        error={<p>Maps Load Error</p>}
        loading={<p>Maps Loading...</p>}
      >
        <NaverMapAPI x={x} y={y} />
      </RenderAfterNavermapsLoaded>
    </>
  );
};

export default NaverMapDis;
